.container
{
    display:grid;
    grid-template-columns: 2fr 1fr;
    max-width: 960px;
    margin:60px auto;
}

.content
{
    padding-left:30px;
}

.sidebar
{
    padding-left:30px;
}

.sidebar form
{
    padding:20px;
    background : #1f9751;
    border-radius: 10px;
}

.sidebar input, .sidebar select
{
    display:block;
    width:100%;
    padding:10px;
    margin-top:8px;
    box-sizing:border-box;
    border:0;
}

.sidebar label
{
    margin:0 auto 20px;
    display:block;
    color:#fff;
}
.sidebar button
{
    color:#fff;
    border:2px solid #fff;
    padding:6px 12px;
    background-color: transparent;
    font-size:1em;
    border-radius: 4px;
    cursor:pointer;
    display:block;
    width:100%;
}

.sidebar h3 {
    color: #1f9751;
    margin-bottom: 20px;
  }
  .sidebar aside {
    margin-top: 40px;
    color: #555;
  }
  .sidebar aside li {
    margin: 10px;
  }
/* transactions */

.transactions li
{
    margin:30px auto;
    border:1px solid #f2f2f2;
    box-shadow: 3px 3px 5px rgba(50,50,50,0.1);
    padding:20px;
    display:flex;
    align-items:center;
    position:relative;
    overflow:hidden;
    border-left:4px solid #1f9751;
}

.transactions .name
{
    color:#777;
    font-size:1.4rem;
}

.transactions .amount
{
    margin-left:auto;
    margin-right:40px;
    color:#777;
    font-weight:bold;
    font-size:1.6rem;
}

.transactions button
{
    position:absolute;
    top:0;
    right:0;
    background:#ddd;
    color:#777;
    border:none;
    padding:12px 8px;
    text-align:center;
    line-height:0;
    font-size:0.9em;
    cursor:pointer;
}

.income_expense
{
    display:flex;
    height:90px;
    align-items: center;
    justify-content: space-around;

}

.income
{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction:column;
    font-size:20px;
}

.income :last-child
{
    color:green;
}

.expense
{
    display:flex;
    align-items: center;
    justify-content: center;
    flex-direction:column;
    font-size:20px;

}
.expense :last-child
{
    color:red;
}

.balance
{
    font-size:25px;
}

 